.listfooter{
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: rgb(234, 240, 245);
}

.gllistfooter{
    width: 100%;
    height: 400px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;

}

.blockfooter{
    height: 100%;
    flex:1;
}
.blockfooterskr{
    height: 100%;
    flex:1;
}

@media screen and (max-width: 1000px){
    .blockfooterskr{
        display: none;
    }
}

.footerblack{
    width: 30px;
    background-color:black;
    color:rgb(150, 150, 150);
    width:100%;
    display: flex;
    justify-content: space-between;
}

.footerblack p{
    margin: 15px 40px 15px 40px;
}